/*--------------------
Leaderboard
--------------------*/
h1 {
  font-size: 18px;
  color: black;
  padding: 12px 13px 18px; }
  h1 svg {
    width: 25px;
    height: 26px;
    position: relative;
    top: 3px;
    margin-right: 6px;
    vertical-align: baseline; }

ol {
  counter-reset: leaderboard; }
  ol li {
    position: relative;
    z-index: 10;
    font-size: 20px;
    counter-increment: leaderboard;
    padding: 18px 50px 18px 50px;
    cursor: pointer;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0) scale(1, 1);
            transform: translateZ(0) scale(1, 1);
    list-style: none; }
    ol li::before {
      content: counter(leaderboard);
      position: absolute;
      z-index: 2;
      top: 15px;
      left: 15px;
      width: 20px;
      height: 20px;
      line-height: 20px;
      color: #c24448;
      background: #fff;
      border-radius: 20px;
      text-align: center;
      list-style: none; }
    ol li mark {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 18px 10px 18px 50px;
      margin: 0;
      background: none;
      color: #fff; }
      ol li mark::before, ol li mark::after {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: -11px;
        left: -9px;
        border-top: 10px solid #c24448;
        border-left: 10px solid transparent;
        transition: all .1s ease-in-out;
        opacity: 0; }
      ol li mark::after {
        left: auto;
        right: -9px;
        border-left: none;
        border-right: 10px solid transparent; }
    ol li small {
      position: relative;
      z-index: 2;
      display: block;
      text-align: right;
      color: #fff; }
    ol li::after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #fa6855;
      box-shadow: 0 3px 0 rgba(0, 0, 0, 0.08);
      transition: all .3s ease-in-out;
      opacity: 0; }
    ol li:nth-child(1) {
      background: #fa6855; }
      ol li:nth-child(1)::after {
        background: #fa6855; }
    ol li:nth-child(2) {
      background: #e0574f; }
      ol li:nth-child(2)::after {
        background: #e0574f;
        box-shadow: 0 2px 0 rgba(0, 0, 0, 0.08); }
      ol li:nth-child(2) mark::before, ol li:nth-child(2) mark::after {
        border-top: 6px solid #ba4741;
        bottom: -7px; }
    ol li:nth-child(3) {
      background: #d7514d; }
      ol li:nth-child(3)::after {
        background: #d7514d;
        box-shadow: 0 1px 0 rgba(0, 0, 0, 0.11); }
      ol li:nth-child(3) mark::before, ol li:nth-child(3) mark::after {
        border-top: 2px solid #b0433f;
        bottom: -3px; }
    ol li:nth-child(4) {
      background: #cd4b4b; }
      ol li:nth-child(4)::after {
        background: #cd4b4b;
        box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.15); }
      ol li:nth-child(4) mark::before, ol li:nth-child(4) mark::after {
        top: -7px;
        bottom: auto;
        border-top: none;
        border-bottom: 6px solid #a63d3d; }
    ol li:nth-child(5) {
      background: #c24448;
      border-radius: 0 0 10px 10px; }
      ol li:nth-child(5)::after {
        background: #c24448;
        box-shadow: 0 -2.5px 0 rgba(0, 0, 0, 0.12);
        border-radius: 0 0 10px 10px; }
      ol li:nth-child(5) mark::before, ol li:nth-child(5) mark::after {
        top: -9px;
        bottom: auto;
        border-top: none;
        border-bottom: 8px solid #993639; }
  ol li:hover {
    z-index: 2;
    overflow: visible; }
    ol li:hover::after {
      opacity: 1;
      -webkit-transform: scaleX(1.06) scaleY(1.03);
              transform: scaleX(1.06) scaleY(1.03); }
    ol li:hover mark::before, ol li:hover mark::after {
      opacity: 1;
      transition: all .35s ease-in-out; }

